/** Page Loader **/
.pace-running .entire-content {
  opacity: 0;
  transition: all 2s ease-in;
}
.pace-done  .entire-content {
  opacity: 1;
}


/** Theme Name: - loading-bar **/
.pace .pace-progress {

  border-radius: 2px;

  transform: translate3d(0, 0, 0);

  display: block;
  position: absolute;
  right: 100%;
  margin-right: -7px;
  width: 93%;
  top: 7px;
  height: 14px;
  font-size: 12px;
  background: #e67e22;
  color: #e67e22;
  line-height: 60px;
  font-weight: bold;
  font-family: "Lato", sans-serif;

}
